------------------------------------------------- */

.screen-reader-text {
	border: 0;
	clip: rect( 1px, 1px, 1px, 1px );
	clip-path: inset( 50% );
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;

	&:hover,
	&:active,
	&:focus {
		color: map-get( $map: $variable_colors, $key: acc-btn-color );
		background-color: map-get( $map: $variable_colors, $key: acc-btn-bgcolor );
		box-shadow: 0 0 2px 2px map-get( $map: $variable_colors, $key: acc-btn-shadow );
		clip: auto !important;
		clip-path: none;

		@include border-radius(0);

		display: block;

		@include font-size(1.4);

		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 1.5rem 2.5rem;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000;
	}
}

#page-section[tabindex="-1"]:focus {
	outline: 0;
}
